<template>
     <div class="container">
        <div class="main__info" v-if="posts">
            <div class="main__info-item" v-for="post in posts" :key="post.id">
                <h2>{{ post.id }}</h2>
                <p>{{ post.title }}</p>
                <p>{{ post.body }}</p>      
            </div>
        </div>
        <div v-else>
            Loading....
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                posts: null
            }
        },
        created() {
            this.getPosts()
        },
        methods: {
           async getPosts() {
               let res = await fetch('https://jsonplaceholder.typicode.com/posts')
               let data = await res.json() 
               this.posts = data
            }
        },
    }
</script>

